.imgset {
    position: relative;
    width: 124px;
    height: 124px;
}
.imgset .imgoverlay {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4) 99%);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
}
.imgset .imgcaption{
    position: absolute;
    bottom: 10px;
    left: 15px;
    color: #fff;
}